
import contents from '../components/contents.vue'
import Navigation from '../components/Navigation.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {
  listDate as contentsDate
} from '../db/contents.js'
import listDate from '../db/list.js'
import dateFormat from 'dateformat';

export default {
  name: 'paryer',
  components: {
    contents,
    Navigation,
    Tab,
    TabItem
  },
  data() {
    let that = this;
    return {
      type: this.$route.params.type,
      values: [],
      todayValues: [],
      list: ['紧急pray', '今天pray'],
      current: '紧急pray',
      orderby: {
        updateDate: 'desc'
      },
      _classification: ''
    }
  },
  methods: {
    filterConditions: function(element, index, array, type) {
      let now = new Date();
      let isEnd = false;
      if (element.endDate) { //如果有结束时间，是否结束
        let d = Date.parse(element.endDate.replace(/-/g, "/"));
        isEnd = d < now.getTime();
      }
      isEnd = element.isEnd || isEnd; //标注结束，或者时间已经结束都是结束
      array[index]["isEnd"] = isEnd;
      let isToday = dateFormat(now, "yyyy-mm-dd") === element.updateDate;
      if (type === 'today') {
        return (!isEnd && isToday);
      } else if (type === 'urgent') {
        return (!isEnd && element.isUrgent);
      } else if (type === 'end') {
        return isEnd;
      } else if (type === 'long') {
        return (!isEnd && element.isLong);
      } else if (type === 'all') {
        return (!isEnd);
      } else {
        return (!isEnd && element.group.indexOf(type) > -1);
      }
    },
    thisNext: function(url) {
      this.$router.push(url)
    },
    initFun: function(_route) {
      let that = this;
      this.type = _route.params.type;
      if (this.type === 'urgent') {
        this.todayValues = contentsDate.filter(function(element, index, array) {
          return that.filterConditions(element, index, array, 'today');
        }).sort(function(a, b) {
          return that.$utils.SortByProps(a, b, that.orderby);
        });
      }
      if (this.type === 'all') {
        this.orderby = {
          label: 'desc',
          updateDate: 'desc'
        }
      }
      this.values = contentsDate.filter(function(element, index, array) {
        return that.filterConditions(element, index, array, that.type);
      }).sort(function(a, b) {
        return that.$utils.SortByProps(a, b, that.orderby);
      });
    }
  },
  watch: {
    '$route' (to, from) {
      this.initFun(to);
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      this.initFun(this.$route);
    })
  },
  created: function(){
    this._classification = "fellowship";
  }
}



